.parallax {
  position: absolute;
  width: 100%;
  height: 100%;
  max-width: 100vw;
  max-height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0;
  margin: 0 auto;
  overflow-x: hidden;
  overflow-y: auto;
  perspective: 1px;
  perspective-origin: center;
}

.parallax-layer-base {
  transform: translateZ(0);
  z-index: 1000;
}

.parallax-layer {
  position: absolute;
  background-size: cover;
  background-position: center;
}

.parallax-layer-back {
  transform: translateZ(-1px) scale(2);
}

.parallax-group {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: visible;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0;
  margin: 0 auto;
  transform-style: preserve-3d;
}

